$on: rgba(map-get($color-index, main_red), 1);
$bg: #D9CB9E;
$off: #8c8c8c;

$ui_height: 20px;

@mixin center {

    position: absolute;
    top :50%;
    left:50%;
    transform: translate(-50%, -50%);
}

@mixin userselect( $value ){


    -webkit-touch-callout: $value;
    -webkit-user-select: $value;
    -khtml-user-select: $value;
    -moz-user-select: $value;
    -ms-user-select: $value;
    user-select: $value;
}

@mixin ui-toggle($height, $on, $off){

    margin: 0;
    padding: 0;

    input[type="checkbox"], input[type="radio"]{
       
        display:none;

        &:checked + label {

            border-color: $on;
            background: $on;
            box-shadow: inset 0 0 0 #{$height / 2} $on;

            > div{

                margin-left: $height;
            }
        }
    }


    label{
        transition: all 200ms ease;
        display: inline-block;
        position: relative;

        @include userselect(none);

        background: $off;
        box-shadow: inset 0 0 0 0 $on;
        border: 2px solid $off;
        border-radius: $height + 2;
        width: $height * 2;
        height: $height + 2px;


        div{

            transition: all 200ms ease;
            background: #fff;
            width: $height;
            height: $height + 2px;
            border-radius: $height / 2;
        }

        &:hover, & > div:hover{

            cursor: pointer;
        }
    }

} 



div.ui-toggle {

        @include ui-toggle($ui_height, $on , $off );

        &.mb-10{

            margin-bottom: 10px;
        }

       
    }

    .meta-container{
        
        position: relative;
        height: $ui_height * 1.5 ;

         .inline{
           

            display: inline-block;
           

        }

        .left{
            position: absolute;
            left: 0;

           
        }

        .right{
            position: absolute;
            right: 0;
        }

        .strong{


            font-weight: bold;
        }


    }

    #gestus_testamonial .inside p:not(.inline){

        margin: 0.5em 0;
        font-size: 15px;
        font-weight: bold;
    }