$color-index: (

        main_blue: #009CA2,
        menu_blue: #BEDFE2,
 
        main_red: #9A1E4F,
        main_grey: #686F71,
        danger: #EF403D,
        warning: #FFCC00,
        success: #28A745,
        lime: #A6CE39,
        green: #48801F,
        orange: #F7941E,
        blue: #286FB2,
        red: #F00 ,
        yellow: #EACB01,
        white: #ffffff,


);


@function color-index($color-key) {
    @return map-get($color-index, $color-key);
}
